body {
  margin: 0;
}

.about-msg {
  font-size: 20px;
  color: #333;
  font-weight: 100;
  text-align: center;
  letter-spacing: 0.5px;
}
.category {
  color: lightseagreen;
}

/* --- Start progress bar --- */

.step-inner {
  font-size: 1rem;
}
.progress {
  margin: 15px;
  height: 20px;
  border: 1px solid black;
  background-color: rgb(194, 193, 193);
  border-radius: 17px;
}

.process-wrapper {
  margin: auto;
  max-width: 1080px;
}

#progress-bar-container {
  position: relative;
  width: 90%;
  margin: auto;
  height: 100px;
  margin-top: 65px;
}

#progress-bar-container ul {
  padding: 0;
  margin: 0;
  padding-top: 15px;
  z-index: 9999;
  position: absolute;
  width: 100%;
  margin-top: -75px;
}

#progress-bar-container li {
  margin-top: 10px;
  list-style: none;
  float: left;
  width: 20%;
  text-align: center;
  color: #aaa;
  text-transform: uppercase;
  font-size: 11px;
  cursor: pointer;
  font-weight: 700;
  transition: all ease 0.2s;
  vertical-align: bottom;
  height: 60px;
  position: relative;
}

/* little dot inside the timeline */
#progress-bar-container li:after {
  content: ' ';
  display: block;
  width: 8px;
  height: 8px;
  margin: auto;
  border: solid 8px rgba(238, 234, 234, 0.9);
  border-radius: 50%;
  margin-top: 28px;
  box-shadow: 0 2px 13px -1px rgba(0, 0, 0, 0.3);
  pointer-events: fill;
}

#progress-content-section {
  width: 90%;
  margin: auto;
  background: #f3f3f3;
  border-radius: 17px;
  padding: 30px 40px;
}

#progress-content-section .section-content h2 {
  font-size: 20px;
  text-transform: uppercase;
  color: #333;
  letter-spacing: 1px;
  text-align: center;
}

#progress-content-section .section-content p {
  font-size: 16px;
  line-height: 1.8em;
  color: #777;
}

#progress-content-section .section-content {
  /* display: none; */
  animation: FadeInUp 700ms ease 1;
  animation-fill-mode: forwards;
  transform: translateY(15px);
  opacity: 0;
}

#progress-content-section .section-content.active {
  display: block;
}

@keyframes FadeInUp {
  0% {
    transform: translateY(15px);
    opacity: 0;
  }

  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
